<template>
	<view>
		<wybar @clickLeft="back" leftText="dddd"></wybar>
		<!-- <view class="tip">{{i18n.mine.editNameTip}}</view> -->
		<scroll-view scroll-y :style="'height:' + windowHeight + 'px'">
			<view class="list_cell" @click="changeLogo()">
				<text class="list_cell_title">{{i18n.mine.shoplogo}}</text>
				<image src="thumbPath" mode="aspectFit" class="list_cell_avatar"></image>
				<image src="../../../static/ads-seld-right.png" mode="aspectFit" class="list_cell_right"></image>
			</view>
			<view class="list_cell" @click="changeName()">
				<text class="list_cell_title">{{i18n.mine.shopName}}</text>
				<text class="list_cell_right_txt">{{shopName}}</text>
					<image src="../../../static/ads-seld-right.png" mode="aspectFit" class="list_cell_right"></image>
					<view class="line"></view>
			</view>		
			<view class="list_cell2" @click="changePhone()">
				<text class="list_cell_title">{{i18n.mine.shopphone}}</text>
				<text class="list_cell_right_txt">{{shopPhone}}</text>
				<image src="../../../static/ads-seld-right.png" mode="aspectFit" class="list_cell_right"></image>
				<view class="line"></view>
			</view>		
			<view class="list_cell2" @click="changeShopType()()">
				<text class="list_cell_title">{{i18n.mine.shoptype}}</text>
				<text class="list_cell_right_txt">{{shopType}}</text>
				<image src="../../../static/ads-seld-right.png" mode="aspectFit" class="list_cell_right"></image>
				<view class="line"></view>
			</view>		
			<view class="list_cell2" @click="changeShopAddress()">
				<text class="list_cell_title">{{i18n.mine.shopaddress}}</text>
				<text class="list_cell_right_txt">{{address}}</text>
				<image src="../../../static/ads-seld-right.png" mode="aspectFit" class="list_cell_right"></image>
				<view class="line"></view>
			</view>		
			<view class="bottomBtn">
				<button  @click="logout()" class="logout">{{i18n.mine.save}}</button>
			</view>		
			<!-- #ifdef H5 -->
			<view style="height: 20px;"></view>
			<!-- #endif -->	
		</scroll-view>
	</view>
</template>

<script>
	import wybar from '../../../components/wybar/wyBar.vue'
	export default {
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				settled_status: 0,
				userInfo: '',
				shopstatus:'22',
				menuButtonHeight: 32,
				top: 0,
				windowHeight: 0,
				refresherTriggered: false,
				islogin:false,
				openVoice:false,
				shopName:'22',
				shopPhone:'11',
				shopType:'22',
				address:'2',
				selectImage:'',
				shoplogo:'',
				shopTypeID:'',
				thumbPath:'',
				isChangeImage:0
			}
		},
		onShareTimeline: function(res) {},
		onShareAppMessage: function(res) {},
		onShow() {
			
			
			var that = this
			
			if(getApp().globalData.userinfo){
				this.userInfo = getApp().globalData.userinfo
			}
			if(this.userInfo){
				this.islogin = true
			}
			
			setTimeout(() => {
				// this.getData()
			}, 0)
			if(uni.getStorageSync('name_new')){
				this.shopName = uni.getStorageSync('name_new')
			}
			if(uni.getStorageSync('phone_new')){
				this.shopPhone = uni.getStorageSync('phone_new')
			}
		},
		onLoad() {
			this.top = getApp().globalData.top
			this.menuButtonHeight = getApp().globalData.menuButtonHeight
			// this.vague_height = getApp().globalData.top + 160
			// #ifdef H5
			this.windowHeight = getApp().globalData.windowHeight - 44
			// #endif
			// #ifndef H5
			this.windowHeight = getApp().globalData.screenHeight - 44
			// #endif
			// #ifdef APP-PLUS
			this.windowHeight = getApp().globalData.screenHeight - 49
			// #endif
			
			uni.setNavigationBarTitle({
				title:this.i18n.mine.profile
			})
		},
		methods: {
			
			login() {
				var that = this
				// #ifdef H5 || APP-PLUS
				uni.navigateTo({
					url: '../login/login',
				});
				// #endif
				// #ifdef MP-WEIXIN || MP-TOUTIAO 
				this.NBlogin.login().then(res => {
					that.userInfo = res
					console.log('userinfo:'+res)
					that.getData()
					that.firstlogin()
				})
				// #endif
			},
			back(){
				uni.navigateBack({
					
				})
			},
			
			changeLogo(){
				let that = this;
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: function(res) {
						let tempFilePaths = res.tempFilePaths;
						var path = tempFilePaths[0];
						console.log(path)
						that.thumbPath = path
						that.isChangeImage = 1
						// that.COS.GetUpload(path, false).then(res => {
						// 	that.avatarPath = path
						// 	that.avatar = res
						// })
					},
				});
			},
			changeName(){
				uni.navigateTo({
					url:'./editName?type=1&content='+this.shopName,
				})
			},
			changePhone(){
				uni.navigateTo({
					url:'./editName?type=2&content='+this.shopPhone,
				})
			},
			changeShopType(){
				uni.navigateTo({
					url:'./shopType',
				})
			},
			changeShopAddress(){
				uni.navigateTo({
					url:'../address/address?content='+this.address,
				})
			},
			save(){
				
			},
			
			getData() {
				var that = this
				this.http.sendRequest('Users.Home').then(info => {
					that.shopName = info[0].name
					that.address = info[0].address
					that.shopPhone = info[0].phone
					that.shopType = info[0].type_name
					that.shoplogo = info[0].thumb
					that.shopTypeID = info[0].type_id
				})
			},
			refresherrefresh() {
				this.refresherTriggered = true;
				this.getData()
				setTimeout(() => {
					this.refresherTriggered = false;
				}, 1000);
			},
		}
	}
</script>

<style>
	@import url("./index.css");
.list_cell_avatar{
	right: 20px;
	width: 30px;
	height: 30px;
	position: absolute;
	border-radius: 15px;
}
</style>
